import moment from 'moment'
import 'moment/locale/ru';
import ScheduleCard from './ScheduleCard';
import {useState} from 'react'
const startOfWeek = moment().startOf('isoWeek');
const endOfWeek = moment().endOf('isoWeek');

const days = [];
let day = startOfWeek;

while (day <= endOfWeek) {
    days.push(day.toDate());
    day = day.clone().add(1, 'd');
}

function getWeek(date) {
    const startOfWeek = moment(date).startOf('isoWeek');
    const endOfWeek = moment(date).endOf('isoWeek');

    const days = [];
    let day = startOfWeek;

    while (day <= endOfWeek) {
        days.push(day.toDate());
        day = day.clone().add(1, 'd');
    }

    return days;
}


function Schedule() {
    moment.locale('ru') 
    const [day, setDays] = useState(days);
    const [currentWeekMove, setWeekMove] = useState(0);
    return (
        <div className="schedule">
            <h1 className="schedule__title">Расписание</h1>
            <div className="d-flex justify-content-between">
            <button  className="navigation-btn navigation-btn--prev "  onClick={() => {
                        setDays(getWeek(moment().day(currentWeekMove-7 )))
                        setWeekMove(currentWeekMove-7)
                    }
                }>Prev</button>
            <button className="navigation-btn navigation-btn--next " onClick={() => {
                        setDays(getWeek(moment().day(currentWeekMove + 7)))
                        setWeekMove(currentWeekMove+7)
                    }
                }>Next</button>
                </div>
            <div className="grid-4 mt-4 mb-4">
            {day.map((day, index) => {
                const currentLang = moment(day).lang("ru").format('dddd');
                console.log(moment(day).day() in [0,6])
                let weekend = [6,0]
                return (
                <ScheduleCard key={index} isWeekend={weekend.includes(moment(day).day())} name={currentLang.charAt(0).toUpperCase() + currentLang.slice(1)} date={moment(day).format('DD-MM-YYYY')} />
            )})}
            </div>
            
            <div className="card">
                <div className="card__header">
                    <h3 className="card__title">Перенос занятий</h3>
                </div>
                <div className="pl-3 pr-3">
                <table className="w-100 mt-4 mb-4 schedule__table">
                    <thead>
                    <tr>
                        <td>Ученики</td>
                        <td>Первоначальная дата</td>
                        <td>Новая дата</td>
                    </tr>
                    </thead>
                    <tbody>
                    <tr>
                        <td>Владислав Тарасов</td>
                        <td>23.05.2021</td>
                        <td>13.06.2021</td>
                    </tr>
                    <tr>
                        <td>Владислав Тарасов</td>
                        <td>23.05.2021</td>
                        <td>13.06.2021</td>
                    </tr>
                    <tr>
                        <td>Владислав Тарасов</td>
                        <td>23.05.2021</td>
                        <td>13.06.2021</td>
                    </tr>
                    </tbody>
                </table>
                </div>
            </div>
            
        </div>
    )
}

export default Schedule;