import Popup from '../../Popup'
import { useDispatch, useSelector } from "react-redux";
import { useEffect, useState, useCallback } from 'react'
import { addTask, editTask } from "../../../action/taskAction"
import PopupButtons from '../../PopupButtons'
import MaterialWidget from '../MaterialWidget'
import TagWidget from '../TagWidget'
function TaskModal() {

   
    const { isModalOpen, tasks, action, editIndex } = useSelector(state =>  ({
        isModalOpen: state.task.isModalOpen,
        tasks: state.task.tasks,
        editIndex: state.task.editIndex,
        action: state.task.action
    }));
    
  
    let modalTitle;
    let handler = () => {};
   
    let task, setTask;
    if (action === 'add') {
        modalTitle = 'Создать задачу'
        handler = (task) => dispatch(addTask({
            isModalOpen: !isModalOpen,
            tasks: [...tasks, task]
        }));
        [task, setTask] = useState({
            id: tasks.length + 1, 
            name:'',
            title:'',
             description:'', 
             tags: [],   
             type: 'task',
        materials: [],
        links: [],
        condinition_files: [],
        decision_files: [],
        hint_files: []
    })
    } else if (action === 'edit') {
        modalTitle = 'Редактировать задачу'
        handler = (task) => {
            dispatch(editTask({
            isModalOpen: !isModalOpen,
            task
        }))
        }
        [task, setTask] = useState({...tasks[editIndex]});
        console.log(task, tasks[editIndex])
    }

    useEffect(() => {
        if (isModalOpen) {
            document.body.style.overflow = 'hidden';
          } else {
            document.body.style.overflow = 'unset';
          }
     }, [isModalOpen]);
 
     useEffect(() => {
        setTask(action === 'edit' ? {...tasks[editIndex]}: {
            id: tasks.length + 1, 
            name:'',
            title:'',
             description:'', 
             tags: [],   
             type: 'task',
        materials: [],
        links: [],
        condinition_files: [],
        decision_files: [],
        hint_files: []
        } )
     }, [tasks[editIndex], action]);

    let memoSetTask = useCallback((task) => {
        setTask(task)
    })
  
    const dispatch = useDispatch();
    return (
      <Popup isSeen={isModalOpen}>
           <button type="button" className="btn-close align-self-end text-dark" aria-label="Close"
            onClick={() => dispatch(addTask({
                
                isModalOpen: !isModalOpen
            }))}
    >
    </button>
    <h1>{modalTitle}</h1>
    <label className="r3">Название</label>
          <input onChange={(evt) => { memoSetTask({...task, title:evt.target.value }) }} value={task.title}    className="input"/>
          <label className="r3">Описание</label>
          <textarea rows="5" onChange={(evt) => { memoSetTask({...task,description: evt.target.value }) }} value={task.description}  className="input" placeholder="Описание"></textarea>
          <label className="r3">Условие</label>
          <input  className="input"/>
          <MaterialWidget tags={task.condinition_files} entity={'task'}
          event={'condinition_files'}/>
          <label className="r3">Подсказки</label>
          <input className="input"/>
          <MaterialWidget  tags={task.hint_files} entity={'task'} event={'hint_files'}/>
          <label className="r3">Решения</label>
          <input className="input"/>
          <MaterialWidget  tags={task.decision_files} entity={'task'} event={'decision_files'}/>
          <label className="r3">Материалы</label>
          <MaterialWidget tags={task.materials} entity={'task'} event={'materials'}/>
          <label className="r3">Ссылки</label>
          <MaterialWidget tags={task.links} entity={'task'} event={'links'}/>
          <TagWidget entity={'task'}/>
          
          <PopupButtons action={action} onClose={() => handler(task)}/>
      </Popup>
    )
}

export default TaskModal;