import { Fragment } from 'react'
import { addMaterialToCourse } from '../../action/courseAction'
import { useSelector, useDispatch } from "react-redux";
import {useState} from 'react'
import PropTypes from 'prop-types';
import { addMaterialToTheme } from '../../action/themeAction';
import { addMaterialToLesson } from '../../action/lessonAction';
import { addMaterialToTask } from '../../action/taskAction';
function TagWidget({entity}) {
    const dispatch = useDispatch()
 
    const handlers = {
        course: addMaterialToCourse,
        theme: addMaterialToTheme,
        lesson: addMaterialToLesson,
        task: addMaterialToTask
    }
    const [newTag, setNewTag] = useState({})
    const storedData = useSelector(state => state[entity][`${entity}s`])
    console.log(storedData);
    const index =  useSelector(state => state[entity].editIndex);
    const tags = storedData[index].tags;
  
    return (
        <Fragment>
             <label className="r3">Теги</label>
        <div className="material__widget justify-content-between pl-3 mt-3">
            <input onKeyPress={(evt) => {
   
    if (evt.key === 'Enter') {
        if (!tags.find(tag => tag.name === newTag.name)){
            storedData.splice(storedData.indexOf(storedData[index]), 1, {...storedData[index], tags: [...tags, { id: tags.length + 1, name: evt.target.value}] })

        }
     
        dispatch(
            handlers[entity](
            { 
                [`${entity}s`]:[...storedData]
            }
            )
        )
        setNewTag({})
    }
  }} onChange={(evt) => {
                setNewTag({ id: tags.length + 1, name: evt.target.value})
            }} placeholder="Введите название тега" className="r3 w-100 mr-3" type="text"/>

            <button  onClick={() => {
                if (!tags.find(tag => tag.name === newTag.name)){
                    storedData.splice(storedData.indexOf(storedData[index]), 1, {...storedData[index], tags: [...tags, {...newTag}] })

                }
             
                dispatch(
                    handlers[entity](
                    { 
                        [`${entity}s`]:[...storedData]
                    }
                    )
                )

                setNewTag({})
               
            }} className="plus-button mr-3">
                +
            </button>
        </div>
        <div className="d-flex mt-3">
        {tags.map(tag => (
            <div key={tag.id + Math.random()} className="btn btn-outline-dark tag mr-3 mb-2">
            {tag.name}
            <button className="btn-close">
    
            </button>
                </div>
        ))}
        </div>
        </Fragment>
    )
}

TagWidget.propTypes = {
    entity: PropTypes.string
}

export default TagWidget;